<template>
  <div>
    <textarea
      class="input"
      :value="props.value"
      @change="handleChange"
    ></textarea>
    <span class="bottom"></span>
    <span class="right"></span>
    <span class="top"></span>
    <span class="left"></span>
  </div>
</template>

<script setup lang="ts">
const props = defineProps({
  /**
   * Actual value to display
   */
  value: {
    type: String,
    required: false,
    default: "",
  },

  /**
   * Callback function on click
   */
  onChangeCallback: {
    type: Function,
    required: true,
  },
});
const handleChange = (event: Event) => {
  const target = event.target as HTMLSelectElement;
  props.onChangeCallback(target.value);
};
</script>

<style scoped>
div {
  position: relative;
  height: 200px;
  width: 100%;
}
textarea {
  background-color: var(--grey-dark3);
  height: 200px;
  width: 100%;
    border: solid 1px;
  color: var(--white);
  border: none;
  outline: none;
  border-radius: 5px;
  opacity: 1;
  text-align: left;
  border: 1px solid transparent;
  transition: background-color 0.3s ease-in-out;
  resize: none;
}
textarea:focus {
  outline: none;
}

textarea::placeholder {
  color: hsla(0, 0%, 100%, 0.6);
}

span {
  position: absolute;
  background-color: var(--primary);
  transition: transform 0.5s ease;
}

.bottom,
.top {
  height: 1px;
  left: 0;
  right: 0;
  transform: scaleX(0);
}

.left,
.right {
  width: 1px;
  top: 0;
  bottom: 0;
  transform: scaleY(0);
}

.bottom {
  bottom: 0;
  transform-origin: bottom right;
}

textarea:focus ~ .bottom {
  transform-origin: bottom left;
  transform: scaleX(1);
}

.right {
  right: 0;
  transform-origin: top right;
}

textarea:focus ~ .right {
  transform-origin: bottom right;
  transform: scaleY(1);
}

.top {
  top: 0;
  transform-origin: top left;
}

textarea:focus ~ .top {
  transform-origin: top right;
  transform: scaleX(1);
}

.left {
  left: 0;
  transform-origin: bottom left;
}

textarea:focus ~ .left {
  transform-origin: top left;
  transform: scaleY(1);
}
</style>
